body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.image {
  position: relative;
  height: 0;
  padding-bottom: 130%;
  /* overflow: hidden; */
  transition: all 0.5s ease;
}
.image img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.swiper-wrapper:hover .swiper-slide .image {
  opacity: 0.5;
  transition: all 0.5s ease;
}
.swiper-wrapper .swiper-slide .image:hover {
  opacity: 1;
  transform: scale(1.1);
  transition: all 0.5s ease;
}
.popup.show {
  display: block;
}
.popup {
  display: none;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.popup-inner {
  max-width: 900px;
  margin: auto;
  max-height: 100vh;
  margin-top: 40px;
  background-color: #fff;
  display: flex;
  flex-direction: row;
}
.close {
  cursor: pointer;
  position: fixed;
  top: 20px;
  right: 20px;
  color:#fff;
  font-size: 40px;
}
.overlay {
  width: 100%;
  height: 100%;
  background-color: #00000057;
  position: absolute;
  z-index: -1;
}
body .swiper{
  position: relative;
  padding: 40px;
}
body .swiper>div{
  overflow-y: visible;
}
.content{
  padding: 20px;
}
.popup .image{
  height: 500px;
  width: 300px;
  padding:0;
}
.popup .image img{
  height: 500px;
  width: 300px;
}
.swiper-button-next,.swiper-button-prev {
  width: 50px;
  height: 50px;
  background: red;
  position: absolute;
  z-index: 9999;
  cursor: pointer;
}
.swiper-button-next {
  right: 0;
  top: 40%;
  transform: translateY(40%);
}
.swiper-button-prev {
  top: 40%;
  transform: translateY(40%);
}
.container-fluid {
  display: flex;
  position: absolute;
  bottom: 0;
}
.navbar-header {
  width: 40%;
  display: flex;
  justify-content: center;
}
ul.nav.navbar-nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 60%;
  background: black;
  margin: 0;
}
.navbar-header img {
  width: 100%;
  height: 100%;
}
.header {
  background-color: #111;
  position: relative;
  padding-top: 56.25%;
}
.header__video {
  position: absolute;
  top: 0;
  left: 0;
}